import { graphql } from "gatsby";

import React from "react";
import CommonHead from "../layout/Head";
import ProductDetailsPage from "../layout/pages/ProductDetailsPage";

import {formatProduct} from "../graphql/products";

const PageTemplate = (props) => {
  const {
    allGoogleSpreadsheetPetitsAnimauxProducts,
    allGooglePhotosPhoto,
  } = props.data;
  
  const data = allGoogleSpreadsheetPetitsAnimauxProducts.nodes[0];
  const product = formatProduct(data, allGooglePhotosPhoto.nodes);

  return (
    <ProductDetailsPage product={product} />
  )
};

export function Head(props) {
  const {
    allGoogleSpreadsheetPetitsAnimauxProducts,
  } = props.data;
  const node = allGoogleSpreadsheetPetitsAnimauxProducts.nodes[0];

  const data = {
    page: {
      name: node.name,
    }
  };

  return <CommonHead {...props} data={data} />;
}

export const query = graphql`
  query PetitsAnimaux($id: String!) {
    allGoogleSpreadsheetPetitsAnimauxProducts(filter: {id: {eq: $id}}) {
      nodes {
        name
        id
        price
        category
        description 
        materialDescription 
        stripeUrl
        coverPhotoId
        width
        height
        length
      }
    }
    
    allGooglePhotosPhoto(filter: {album: {title: {eq: "P2F"} } }) {
      nodes {
        id
        file {
          childImageSharp {
            gatsbyImageData(
              height: 500
            )
          }
          publicURL
        }
        description
      }
    }

  }
`;

export default PageTemplate;